<script>
import $ from 'jquery';
import axios from "axios";
export default {
  name: "BusinessSidebar",
  data () {
    return {
      collapseOnScroll: true,
      value: true,
      userdata: [],
      userdatas: [],
      ruserdata: [],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
    }
  },
  created() {
    this.getuserdata();
    this.isAuthenticated();
    this.user_id();
    this.ruserdata =  this.user_id();
    this.buser_data();
    this.userdata = this.buser_data();
    this.buser_role();
    if(this.isAuthenticated() == true && this.buser_role() == 1){
        console.log('its ok');
      // this.$router.push({name: 'usersidebar'});
    }else {
      // this.$router.push({name: 'businesslogin'});
      console.log('not ok');
    }
  },
  computed: {
    lastWord() {
      const words = this.userdatas.name.split(' ');
      return words[words.length - 1];
    },
    firstCharOfLastWord() {
      return this.lastWord.charAt(0);
    }
  },
  methods: {
    getuserdata(){
      axios.get('userdata')
          .then((resp) =>{
        this.userdatas = resp.data.user;
      })
    },
    businessLogout() {
      this.$store.dispatch('logout');
      this.$router.push('/business/login');
    },
    isAuthenticated() {
      return this.$store.state.isAuthenticated;
    },
    buser_role() {
      return this.$store.state.userData.user_role;
    },
    buser_data() {
      return this.$store.state.userData;
    },
    user_id() {
      return this.$store.state.userId;
    },
    toggleValue() {
      this.value = !this.value; // Toggle the value between true and false
    },
  },
  watch: {
    value(newValue) {
      console.log('Value changed:', newValue);
    },
  },
}
$(document).ready(function(){
  $(".v-app-bar__nav-icon").click(function(){
    $("main").toggleClass("love");
  });
});


</script>

<style scoped>
.love{
  padding: 10px 0 0 260px !important;
  }
@media (min-width: 577px) {
  .bdash > div{
    height: calc(100vH - -50px);
    top: 155px;
    /*width: 240px;*/
    overflow: auto;
    transform: translateX(0%);
    position: fixed;
    z-index: 2;
  }
  .bdash .v-list{
    padding: 5px 0 !important;
    }
  .bright{
    max-width: calc(100vw - 183px - calc(var(--bs-gutter-x) * 0.5));
    width: 100%;
    left: 183px;
    position: absolute;
  }
  .bleft{
    max-width: 183px;
    min-width: 183px;
  }
  }
@media (max-width: 576px) {
  .love {
    padding: 2px !important;
    }
  .bdash > div{
    height: calc(100vH - -50px);
    top: 121px;
    width: 60px;
    overflow: auto;
    transform: translateX(0%);
    position: fixed;
  }
  .bright{
    max-width: calc(100vw - 60px);
  }
  .bleft{
    max-width: 60px;
    min-width: 60px;
  }
}
</style>